@import "vendor/foundation/import-variables";
// SupportLoading.scss

:local .support-loading {

    position: relative;
    height: 32em;

    .loading-box {
        margin: 3em auto 0 auto;
        width: 25em;
        height: 12em;
        color: #444;

        hr {
            margin: 10px -3em;
            border-color: #6dc16d;

            &.error {
                border-color: #b72020;
            }
        }

        p.wait-text {
            text-align: center;
        }

    }

}
