@import "vendor/foundation/import-variables";
@import "../../../../../sass/_colors.scss";
@import "../../../../../sass/vendor/foundation/import-variables";
@import "~foundation-sites/scss/components/button";

/* suppress IE scrollbar */
.assetRegistry {
    overflow: hidden;
}

.addressDisplay {
    margin-bottom: .75em;

    hr {
        border-bottom: 1px solid #CCC;
    }
}
.addressText {
    line-height: 1.2em;
    margin-top: .5em;
    margin-bottom: .5em;
    margin-left: .75em;
}

.confirmation {
    display: inline-block;
    font-weight: normal;
    line-height: 1.25em;
}

h2.addressCard {
    margin-top: 0em;
    margin-bottom: 0em;
}

button.edit, button.new {
    /* push down past the select field label */
    margin-top: 2em;
}

.form-label.error {
    color: #F04953;
}

.equalHeight {
    margin-bottom: 1em;
}

.cardAddress
{
    overflow: visible;
    height: 100%;
}

hr.card {
    margin-top: 1em;
    margin-bottom: 1em;
}

.sla {
    color: #757575;
    margin-bottom: -0.5em;
}

.error {
    label {
        color: $error-color;
    }
    :global {
        .Select-control {
            border: 1px solid $error-color;
        }
    }
}

.search-container {
    display: flex;
    background: none;
    margin-left: 1.5em;
    margin-top: 1em;
}

.search-icon-container {
    flex-grow: 0;

    label {
        font-size: 1.5em;
    }
}
.search-input-container {
    position: relative;
    flex-grow: 1;
    margin-left: 1em;
}



input.search[type='text'],
input.search[type='text']:focus {
    font-size: 1.5em;
    height: 32px;
}

.cancel-search {
    position: absolute;
    top: 0px;
    right: 0px;
    color: #6d6d6d;
    text-align: center;
    text-decoration: none;
    padding: 10px;
}

.addressDetailForm {
    hr {
        margin-top: 1.5em;
        margin-bottom: 1em;
        border-bottom: 1px solid #CCC;
    }
    h2 {
        font-weight: normal;
    }
    ul li {
        display: inline;
        margin-right: 1em;
    }
}
