@import "vendor/foundation/import-variables";
@import "../../../sass/vendor/foundation/import-variables";
@import "~foundation-sites/scss/components/pagination";

:global .nmbl-Pagination {
    @include foundation-pagination;

    .pagination {
        display: inline-block;
        padding: 0;
    }

    .pagination-previous,
    .pagination-next {
        width: 1.25rem;
        height: 1rem;
        text-align: center;
        line-height: 1;

        > a {
            color: inherit;
            text-decoration: none;

            &:not(.disabled):hover {
                color: $white;
                border-color: $pagination-item-background-hover;
            }

            &.disabled {
                cursor: default;
            }
        }
    }

    .pagination-previous > a {
        border-bottom-left-radius: 2px;
        border-top-left-radius: 2px;
        border: 1px solid $medium-grey-03;
    }

    .pagination-next > a {
        border-bottom-right-radius: 2px;
        border-top-right-radius: 2px;
        border: 1px solid $medium-grey-03;
        border-left: 0;
    }

    .pagination-previous > a::before,
    .pagination-next > a::after {
        content: none;
    }

    color: $dark-grey-02;
    margin: 0;

    .nmbl-Pagination__count {
        color: $medium-grey-01;
    }

    .nmbl-Pagination__range {
        margin-right: 10px;
    }
}
