:local .add-comment .textbox-wrapper {
  position: relative;
  width: 70%;
}

:local .add-comment .textbox-wrapper textarea {
  width: 100%;
  height: 175px;
}

:local .add-comment .textbox-wrapper .sending {
  top: 20px;
  left: 200px;
  position: absolute;
}

:local .add-comment button {
  font-size: 14px;
  margin-right: 1em;
}

:local .add-comment.busy-sending textarea {
  opacity: 0.25;
}

