@import "vendor/foundation/import-variables";
.legacy-breadcrumbs {
    :global {
        .breadcrumbs-region {
            font-size: 18px;
            font-weight: 700;
            margin: 0px;
            position: relative;
        }

        .breadcrumb {
            color: rgb(102, 102, 102);
            font-weight: bold;
            margin-bottom: 5px;
            padding: 8px 15px 5px 15px;
        }
        .breadcrumb > li {
            display: inline-block;

            &:not(:last-of-type):after {
                content: "\00a0/\00a0";
            }
        }
        .breadcrumb > .active {
            color: rgb(102, 102, 102);
        }
    }
}
