@import "vendor/foundation/import-variables";
// SupportListCases.scss


// $interface-highlight: rgb(123, 114, 233)
$interface-highlight: #4caf50;


:local .support-list-cases {

    font-size: 1em;

    .status-banner {
        position: relative;
        background-color: #eee;
        padding: 30px 0 30px 5%;
        margin-bottom: 24px;

        h2 {
            font-size: 28px;
            margin-bottom: 4px;
        }
        // h3 { font-size: 18px; }

        .actions-options {
            position: absolute;
            top: 38px; right: 5%;

            button {
                font-size: 14px;
                font-weight: 400;
                margin: 0 0 0 0.5em;

                > i {
                    margin-right: 6px;
                }
            }
        }
    }


    .grid-inset {
        margin: 0 5%;
        // padding: 0 1px;
    }


    :global .nmbl-grid-view {

        // column sizing overides
        table {
            th[data-colname="priority"]       { width: 8em; }
            th[data-colname="assetName"]        { width: 10em; }
            th[data-colname="caseNumber"]     { width: 7.5em; }
            th[data-colname="status"]         { width: 9em; }
            th[data-colname="ownerName"]          { width: 13em; }
            th[data-colname="createdDateOnly"]    { width: 11em; }
            th[data-colname="lastUpdatedDate"]    { width: 11em; }
        }

        // alternate row shading
        table {
            tr[data-odd] {  background-color: #fff; }
            tr[data-even] { background-color: #f7f7f7; }
        }

        table tr th input,
        table tr th select {
            border: 1px solid #a6a6a6;
            height: 27px;
        }

        table tr th span {
            input {
                font-size: 12px;
            }
            button {
                border: 1px solid #a6a6a6;
                margin-left: 2px;
                height: 22.5px;
                position: relative;
                top: -1px;
                background-color: #ffffff;
            }
        }
    }

    .filter-form {

        // background-color: #f0f0f0;
        padding: 1em 1.5em;

        label {
            margin-right: 4em;

            input[type=checkbox] {
                margin-right: 1em;
            }
        }

        input[type=text],
        textarea {
            font-size: 0.9em;
            padding: 0.25em 0.5em;
        }

    }


    .top-buttons {


        .holdit {
            position: relative;
            display: inline-block;
            text-align: center;
            width: 9em;
            vertical-align: top;

            color: #333;
            a { color: #333; }

            .biggie {

                font-size: 2em;
                display: block;
                cursor: pointer;

                width: 4em;
                height: 2em;
                line-height: 2em;

                margin: 0.25em;
                padding: 0 0.5em;

                color: $interface-highlight;
                font-weight: 300;
                text-align: center;

                // background-color: $interface-highlight;
                background-color: transparent;

                border-radius: 4px;
                border: 2px solid $interface-highlight;
                box-sizing: border-box;

                transition-duration: 0.3s;
                transition-property: all;

                &.selected {
                    color: white;
                    background-color: $interface-highlight;
                }
            }

        }

        .create-case {

            display: block;
            cursor: pointer;

            width: 12em;
            height: 2.5em;
            line-height: 2.5em;

            margin: 2em 0.25em 0.25em 2em;
            padding: 0 0.5em;

            color: white;
            font-weight: 300;
            text-align: center;

            background-color: $interface-highlight;
            // background-color: transparent;

            border-radius: 4px;
            border: 2px solid $interface-highlight;
            box-sizing: border-box;

        }

    }

}
