@import "vendor/foundation/import-variables";
.container {
    font-size: $next-global-font-size;

    // Legacy styles
    :global {
        .permissions-view {
            .requested-grants {
                dl {
                    $width: 100px;
                    dt {
                        width: $width;
                        float: left;
                        clear: left;
                        text-align: right;
                        margin-bottom: 0;
                    }
                    dd {
                        margin-left: ($width + 10px);
                    }
                }
            }

            .grid-view {
                h4 {
                    margin: 0px;
                }
            }

            [data-region = "requested"], [data-region = "inbound"], [data-region = "outbound"] {
                > div {
                    margin-bottom: 3em;
                }
            }

        }
    }
}

