@import "vendor/foundation/import-variables";
// SupportTab.scss

:local .support-tab {

    position: relative;
    // margin: -5px -15px; // visually overrides #content-region

    // typeography for section ------------------
    // https://confluence.lab.nimblestorage.com/display/UXPatternLibrary/Typography

    font-size: 13px;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: #444;
    background-color: #FFF;
    min-height: 100%;
    width: 100%;


    h1,h2,h3,h4,h5 { margin: 0; }

    h1 { font-size: 24px; }
    h2 { font-size: 20px; }
    h3 { font-size: 16px; }
    h4 { font-size: 13px; font-weight: 700; }
    h5 { font-size: 11px; text-transform: uppercase; }

    .support-content {

        // margin-left: 30px;  // $navpanel-edge;
        padding: 0;
        
        input, textarea {
            font-size: 1.25em;
            font-weight: 200;
            padding: 0.5em 0.75em;
        }

        :global .nmbl-grid-view {
            input {
                font-size: 1em;
                padding: 0.25em 0.5em;
            }

        }

    }

}
