:global .nmbl-Toggle {
  font-size: 0.75rem;
}

:global .nmbl-Toggle__button {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 1rem 0;
  padding: 0.85em 1em;
  -webkit-appearance: none;
  border: 1px solid transparent;
  border-radius: 0;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  font-size: 0.9rem;
  line-height: 1;
  text-align: center;
  cursor: pointer;
  background: #ccc;
  color: #000;
  border-radius: 0;
  margin-right: 1px;
  font-size: inherit;
  padding: 7px 15px;
}

[data-whatinput='mouse'] :global .nmbl-Toggle__button {
  outline: 0;
}

:global .nmbl-Toggle__button:hover,
:global .nmbl-Toggle__button:focus {
  background: #adadad;
  outline: none;
}

:global .nmbl-Toggle__button.nmbl-Toggle__first {
  border-radius: .2rem 0 0 .2rem;
}

:global .nmbl-Toggle__button.nmbl-Toggle__last {
  border-radius: 0 .2rem .2rem 0;
  margin-right: 0;
}

:global .nmbl-Toggle__button.nmbl-Toggle--selected {
  background: #444;
  color: #fff;
}

