@import "vendor/foundation/import-variables";
// SearchNav.scss
@import "../../../../../sass/vendor/foundation/import-variables";
@import "~foundation-sites/scss/components/button";

:local .search-nav {

    position: relative;
    padding: 30px 0 85px 5%;
    height: 6em;

    .actions-options {
        position: absolute;
        top: 30px;
        right: 5%;

        button {
            font-size: 15px;
            font-weight: 400;
            height: 33px;
            margin: 0 0 0 0.5em;

            > i {
                margin-right: 6px;
            }
        }
    }

    .case-owner {
        position: absolute;
        top: 30px;

        :global {
            .cases-by {
                $btn-color: $brand-color;
                $active-btn-color: $primary-color;

                button {
                    color: white;
                    font-size: 15px;
                    background-color: $btn-color;
                    border-color: #999;
                    height: 33px;

                    &:hover,
                    &:focus  {
                        background-color: darken($btn-color, 12%);
                    }

                    &.active {
                        background-color: darken($active-btn-color, 12%);
                        border-color: darken($active-btn-color, 14%);
                    }
                }
            }
        }

        .filter-label {
            font-weight: bold;
            padding-right: 5px;
        }
    }

    .search-stuff {
        width: 35%;
        margin: 0 auto;

        input.search-bar {
            display: block;
            width: 90%;
            font-size: 12px;
            border: .5px solid rgb(166, 166, 166);
            border-radius: 0.5em;
            height: 33px;
        }

        .cancel-search {
            position: absolute;
            top: 30px;
            right: 35%;
            color: #6d6d6d;
            width: 20px;
            height: 20px;
            text-align: center;
            text-decoration: none;
            padding: 6px;
        }

    }

    .hide-auto-cases {
        position: absolute;
        top: 70px;
        opacity: 1;

        input {
            margin-right: 5px;
        }
    }

    &.is-internal {
        .hide-auto-cases { top: 35px }
    }

    .local-stuff {
        background-color: #eee;
        height: 4em;
    }

}
