@import "vendor/foundation/import-variables";
// QuickTable.scss

:global {

    .nmbl-quicktable {
        display: table;
        border-collapse: collapse;
    }

    .quicktable-caption {
        display: table-caption;
        width: 100%;
        padding: 2px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        // border-bottom: 1px solid #ddd;
    }
    
    .quicktable-row {
        display: table-row;


        .qtrc-first {
            display: table-cell;
            font-weight: 600;
            padding: 2px;
            margin-bottom: 1px;

            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;

        }

        .qtrc-next {
            display: table-cell;
            font-weight: 200;
            padding: 2px;
            margin-bottom: 1px;
        }
    }

}
