:global .nmbl-quicktable {
  display: table;
  border-collapse: collapse;
}

:global .quicktable-caption {
  display: table-caption;
  width: 100%;
  padding: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

:global .quicktable-row {
  display: table-row;
}

:global .quicktable-row .qtrc-first {
  display: table-cell;
  font-weight: 600;
  padding: 2px;
  margin-bottom: 1px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:global .quicktable-row .qtrc-next {
  display: table-cell;
  font-weight: 200;
  padding: 2px;
  margin-bottom: 1px;
}

