:local .support-list-cases {
  font-size: 1em;
}

:local .support-list-cases .status-banner {
  position: relative;
  background-color: #eee;
  padding: 30px 0 30px 5%;
  margin-bottom: 24px;
}

:local .support-list-cases .status-banner h2 {
  font-size: 28px;
  margin-bottom: 4px;
}

:local .support-list-cases .status-banner .actions-options {
  position: absolute;
  top: 38px;
  right: 5%;
}

:local .support-list-cases .status-banner .actions-options button {
  font-size: 14px;
  font-weight: 400;
  margin: 0 0 0 0.5em;
}

:local .support-list-cases .status-banner .actions-options button > i {
  margin-right: 6px;
}

:local .support-list-cases .grid-inset {
  margin: 0 5%;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="priority"] {
  width: 8em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="assetName"] {
  width: 10em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="caseNumber"] {
  width: 7.5em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="status"] {
  width: 9em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="ownerName"] {
  width: 13em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="createdDateOnly"] {
  width: 11em;
}

:local .support-list-cases :global .nmbl-grid-view table th[data-colname="lastUpdatedDate"] {
  width: 11em;
}

:local .support-list-cases :global .nmbl-grid-view table tr[data-odd] {
  background-color: #fff;
}

:local .support-list-cases :global .nmbl-grid-view table tr[data-even] {
  background-color: #f7f7f7;
}

:local .support-list-cases :global .nmbl-grid-view table tr th input,
:local .support-list-cases :global .nmbl-grid-view table tr th select {
  border: 1px solid #a6a6a6;
  height: 27px;
}

:local .support-list-cases :global .nmbl-grid-view table tr th span input {
  font-size: 12px;
}

:local .support-list-cases :global .nmbl-grid-view table tr th span button {
  border: 1px solid #a6a6a6;
  margin-left: 2px;
  height: 22.5px;
  position: relative;
  top: -1px;
  background-color: #ffffff;
}

:local .support-list-cases .filter-form {
  padding: 1em 1.5em;
}

:local .support-list-cases .filter-form label {
  margin-right: 4em;
}

:local .support-list-cases .filter-form label input[type=checkbox] {
  margin-right: 1em;
}

:local .support-list-cases .filter-form input[type=text],
:local .support-list-cases .filter-form textarea {
  font-size: 0.9em;
  padding: 0.25em 0.5em;
}

:local .support-list-cases .top-buttons .holdit {
  position: relative;
  display: inline-block;
  text-align: center;
  width: 9em;
  vertical-align: top;
  color: #333;
}

:local .support-list-cases .top-buttons .holdit a {
  color: #333;
}

:local .support-list-cases .top-buttons .holdit .biggie {
  font-size: 2em;
  display: block;
  cursor: pointer;
  width: 4em;
  height: 2em;
  line-height: 2em;
  margin: 0.25em;
  padding: 0 0.5em;
  color: #4caf50;
  font-weight: 300;
  text-align: center;
  background-color: transparent;
  border-radius: 4px;
  border: 2px solid #4caf50;
  box-sizing: border-box;
  transition-duration: 0.3s;
  transition-property: all;
}

:local .support-list-cases .top-buttons .holdit .biggie.selected {
  color: white;
  background-color: #4caf50;
}

:local .support-list-cases .top-buttons .create-case {
  display: block;
  cursor: pointer;
  width: 12em;
  height: 2.5em;
  line-height: 2.5em;
  margin: 2em 0.25em 0.25em 2em;
  padding: 0 0.5em;
  color: white;
  font-weight: 300;
  text-align: center;
  background-color: #4caf50;
  border-radius: 4px;
  border: 2px solid #4caf50;
  box-sizing: border-box;
}

