@import "vendor/foundation/import-variables";
$panel-default-border: #cdcdcd;
$font-size: 16px;

.container {
    font-size: $font-size;
    overflow-x: hidden;

    :global {
        .label {
            display: inline;
            padding: .2em .6em .3em;
            //font-size: 75%;
            font-weight: bold;
            line-height: 1;
            //color: $white;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: .25em;
        }

        .label-warning {
            background-color: $orange-01;
            color: $white;
        }

        .breadcrumb .fa-info-circle {
            margin-left: 6px;
        }

        .lab-app {
            position: relative;

            button.feedbacking {
                transition: color 0.1s linear, background-color 0.1s linear;
                margin-right: 10px;

                i {
                    margin-left: 4px;
                    vertical-align: bottom;
                }
            }

            .shiny-container {
                position: relative;
                border: 1px solid $panel-default-border;

                iframe {
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    height: 100%;
                    width: 100%;
                    border: 0;
                }

                .lab-app-loading {
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    background: url(../../../../../images/labs-loading.gif) no-repeat center center;
                    background-size: 100px;
                }

            }

        }

        .labs-view {

            //$labs-background: #fafafa;

            position: relative;
            margin: 0px -16px 0 -16px; // negative margins to paint over the .content-region
            padding-bottom: 180px;
            //background-color: $labs-background;

            .label {
                text-transform: uppercase;
            }

            /**
             * Labs Landing Page Menu
             */

            .labs-landing {

                -webkit-font-smoothing: antialiased; // request from Judy Wu (from UX)

                $labs-landing-app-width: 350px;
                $labs-landing-app-height: 200px;
                $labs-app-spacing-lr: 10px;
                $labs-app-spacing-tb: 10px;
                $labs-landing-apps-per-row: 3;
                $labs-landing-width: ($labs-landing-app-width + 2 * $labs-app-spacing-lr) * $labs-landing-apps-per-row;

                $labs-nav-border: 1px solid #ccc;
                $labs-nav-color: #444;

                width: $labs-landing-width;
                margin: 0 auto;

                // this is designed to conceal the breadcrumbs on the landing page level
                .banner {
                    position: relative;
                    height: 106px;

                    background-size: 130px;
                    background-repeat: no-repeat;
                    background-position: 45% 72%;
                    background-image: url(../../../../../images/labs-logo.svg);

                    .label {
                        position: absolute;
                        top: 54px;
                        right: 472px;
                    }
                }

                h1 {
                    //font-size: 20px;
                    margin: 0 0 26px 0;
                    color: $labs-nav-color;
                    text-align: center;
                }

                // generic flat list
                ul {
                    margin: 0;
                    list-style: none;
                    padding: 0;
                }

                /*
                Menu bar
                height: 30px
                top and bottom 1px line: #ccc
                Selected menu item: 11px Bold #444
                Default menu items: 11px #888, on hover, #444
                */

                ul.labs-nav {

                    margin-left: $labs-app-spacing-lr;
                    margin-right: $labs-app-spacing-lr;

                    margin-bottom: 10px;

                    padding: 4px 0;
                    border-top: $labs-nav-border;
                    border-bottom: $labs-nav-border;
                    text-align: center;
                    text-transform: uppercase;

                    li {
                        display: inline;
                        margin: 0 1em; // 1em ~ 16px so we get ~32px between
                        a {
                            //font-size: 11px;
                            color: $labs-nav-color;
                            cursor: pointer;

                            &:hover {
                                text-decoration: underline;
                            }

                            &.selected {
                                font-weight: bold;
                            }

                            &:active,
                            &.selected {
                            }

                        }
                    }
                }

                ul.labs-apps {
                    li {

                        font-size: 1em;
                        display: inline-block;
                        vertical-align: top;

                        a {
                            position: relative;
                            display: block;
                            margin: $labs-app-spacing-tb $labs-app-spacing-lr;
                            padding: 30px;
                            cursor: pointer;
                            background-color: #fff;

                            width: $labs-landing-app-width;
                            height: $labs-landing-app-height;
                            overflow: auto;

                            border: 1px solid transparent;
                            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.15);
                            transform: translate(0, 0);
                            transition: transform 0.1s ease-in-out;

                            &:hover {
                                text-decoration: none;
                                border: 1px solid $anchor-color;
                                box-shadow: 0 2px 5px 0 rgba(25, 115, 203, 0.5);
                                transform: translate(0, -2px);
                            }

                            &:focus {
                                outline: none;
                                text-decoration: none;
                            }

                            h3 {
                                //font-size: 13px;
                                color: $anchor-color;
                                font-weight: 700;
                                margin: 0 0 6px 0;;
                                padding: 0;
                            }

                            .starset {
                                height: 14px;
                                margin: 4px 0;
                            }

                            p {
                                //font-size: 13px;
                                color: #444;
                            }

                            .lab-app-views {
                                //font-size: 11px;
                                color: #888;
                            }

                            .label {
                                position: absolute;
                                top: 5px;
                                right: 5px;
                            }

                        }
                    }
                }

            }

        }

        // for the individual apps
        .lab-app {

            iframe {
                border: none;
            }

        }

        #app[data-user-class="external"] ul.labs-apps {
            li[data-app-id="performance_recommendations_storage"] {
                display: none !important;
            }
        }

    }
}

:global {
    .lab-app-feedback {
        font-size: $font-size;

        #contact-form {

            .form-group {
                margin-bottom: 24px;
            }

            label {
                margin-top: 0;
                padding-top: 0;
                padding-bottom: 7px;
            }

            .frequency label {
                margin-right: 14px;
                font-weight: normal;
                cursor: pointer;
            }

        }

        .labs-ratings-template {
            font-size: 1.25em;
            position: absolute;
            right: 10px;
            top: 2px;

            a {
                color: #ccc;
                display: inline-block;
                text-decoration: none;
                margin: 0 4px;

                &.fa-thumbs-up:hover,
                &.fa-thumbs-up.selected {
                    color: $brand-color;
                }

                &.fa-thumbs-down:hover,
                &.fa-thumbs-down.selected {
                    color: $red-01;
                }
            }

        }

        // defines the general display behavior for the entire group of stars
        .rating {
            font-size: 1em;
            cursor: pointer;

            // defines a single "star" that's made from a filled and outlined glyph overlaid
            .starduo {
                font-size: 1.5em;
                position: relative;
                display: inline-block;
                width: 1em;
                height: 1em;

                // .fa[class^=fa-star] {
                .fa.fa-star,
                .fa.fa-star-o {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 1em;
                    height: 1em;
                }

                // the default "off" state for the star
                .fa-star {
                    color: transparent;
                }
                .fa-star-o {
                    color: #777;
                }

            }

            // defines the "on" state for the star
            .star-is-on {
                .fa-star {
                    color: $orange-01;
                }
                .fa-star-o {
                    color: transparent;
                    visibility: hidden;
                }
            }

            // highlights the correct number of starduos depending on the category score
            &[data-score-current="1"] {
                .starduo[data-score="1"] {
                    @extend .star-is-on;
                }
            }

            &[data-score-current="2"] {
                .starduo[data-score="1"],
                .starduo[data-score="2"] {
                    @extend .star-is-on;
                }
            }

            &[data-score-current="3"] {
                .starduo[data-score="1"],
                .starduo[data-score="2"],
                .starduo[data-score="3"] {
                    @extend .star-is-on;
                }
            }

            &[data-score-current="4"] {
                .starduo[data-score="1"],
                .starduo[data-score="2"],
                .starduo[data-score="3"],
                .starduo[data-score="4"] {
                    @extend .star-is-on;
                }
            }

            &[data-score-current="5"] {
                .starduo[data-score="1"],
                .starduo[data-score="2"],
                .starduo[data-score="3"],
                .starduo[data-score="4"],
                .starduo[data-score="5"] {
                    @extend .star-is-on;
                }
            }

            // hovering on the stars to select a value
            &[data-score-current]:hover .starduo[data-score]:hover {
                .fa-star {
                    color: $orange-01;
                }
                .fa-star-o {
                    color: $red-01;
                    visibility: visible;
                }

            }

            // clicked on a star
            &[data-score-current]:hover .starduo[data-score]:active {
                .fa-star {
                    color: $red-01;
                }
                .fa-star-o {
                    color: $orange-01;
                    visibility: visible;
                }

            }

        }
    }

    .lab-app-user-guide {
        font-size: $font-size;

        .modal-body > ul {
            padding-left: 40px;
        }
    }
}
