@import "vendor/foundation/import-variables";
@import "../../../sass/vendor/foundation/import-variables";
@import "~foundation-sites/scss/components/button";

:global {
    // MJD TODO trying a technique out for Lenovo rebanding
    $nmbl-toggle-button-background-color: $medium-grey-03;
    $nmbl-toggle-button-color: $black;
    $nmbl-toggle-button-selected-background-color: $dark-grey-02;
    $nmbl-toggle-button-selected-color: $white;

    .nmbl-Toggle {
        font-size: map-get($button-sizes, small);
    }

    .nmbl-Toggle__button {
        @include button-base;

        background: $nmbl-toggle-button-background-color;
        color: $nmbl-toggle-button-color;
        border-radius: 0;
        margin-right: 1px;
        font-size: inherit;
        padding: 7px 15px;

        &:hover,
        &:focus {
            background: scale-color($nmbl-toggle-button-background-color, $lightness: -15%);
            outline: none;
        }

        &.nmbl-Toggle__first {
            border-radius: .2rem 0 0 .2rem;
        }

        &.nmbl-Toggle__last {
            border-radius: 0 .2rem .2rem 0;
            margin-right: 0;
        }

        &.nmbl-Toggle--selected {
            background: $nmbl-toggle-button-selected-background-color;
            color: $nmbl-toggle-button-selected-color;
        }
    }
}
