@import "vendor/foundation/import-variables";
@import "~foundation-sites/scss/components/switch";

@include foundation-switch;

.switch-with-status {
    display: flex;
    align-items: center;

    .switch {
        margin-bottom: 0px;
        margin-right: 10px;
        display: inline-block;
    }
}

.text-alert {
    color: $alert-color;
}

.text-success {
    color: $success-color;
}

.telemetry-settings {
    ul {
        margin-left: 1.25rem;
    }
}
