:local .rma-labels {

    :global .nmbl-quicktable {
        margin: 1em 0 1.5em 0;

        .quicktable-row:first-child {
            .qtrc-first {
                font-weight: bold;
            }

            .qtrc-next {
                font-weight: bold;
            }
        }

        .qtrc-first {
            font-weight: normal;
            padding-right: 25px;
        }

        .qtrc-next {
            font-weight: normal;
            padding-right: 25px;
        }
    }
}
