@import "vendor/foundation/import-variables";
// CaseInformation styles

:local {

    .case-information {

        .case-banner {
            position: relative;

            padding: 28px 0 36px 10%;
            margin-bottom: 1.9em;

            background-color: #eee;

            h4 {
                margin-bottom: 2px;
                font-weight: 400;
                font-size: 1.2em;
            }
            h2 {
                font-size: 28px;
                margin-top: 4px;
            }

            .support-logo {
                position: absolute;
                top: 26px; left: 3.5%;
                width: 48px;
                height: 48px;
                border-radius: 50%;
                padding-top: 9px;

                // default
                // &.open {}
                background: #fff url("../../../../../../images/case-open.png") center center no-repeat;
                background-size: 32px;

                &.closed {
                    background-image: url("../../../../../../images/case-closed.png");
                }
            }
        }

        .page-inset {
            margin: 0 10%;
        }

        .action-set {

            // $interface-highlight: #4caf50;

            // color: $interface-highlight;

            button {
                font-size: 14px;
                font-weight: 400;
                margin: 0 0.5em 0 0;
                width: 118px;

                > i {
                    margin-right: 2px;
                }
            }

            // button,
            // button:focus,
            // button:active {
            //     border: 1px solid $interface-highlight;
            //     color: $interface-highlight;
            // }

            // button:hover,
            // button.selected {
            //     color: white;
            //     background-color: $interface-highlight;
            //     border: 1px solid $interface-highlight;
            // }

        }

    }

}
