@import "vendor/foundation/import-variables";
@import "~foundation-sites/scss/forms/forms";

.user-management {
    font-size: $next-global-font-size; // TODO Remove once global font size is changed
}

.footer-pagination {
    padding-bottom: 60px;
}

.settings-popup {
    font-size: $next-global-font-size; // TODO Remove once global font size is changed

    .form-error {
        // We'd prefer to just use the global .form-error, however "base.scss" in elmer overrides other form styles without using foundation variables
        @include form-error;
        display: block;
        margin-top: 0;
    }

    label {
        text-align: right;
    }

    label, input, select {
        font-size: inherit; // TODO Remove once global font size is changed
    }
}
