:local .support-tab {
  position: relative;
  font-size: 13px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #444;
  background-color: #FFF;
  min-height: 100%;
  width: 100%;
}

:local .support-tab h1,
:local .support-tab h2,
:local .support-tab h3,
:local .support-tab h4,
:local .support-tab h5 {
  margin: 0;
}

:local .support-tab h1 {
  font-size: 24px;
}

:local .support-tab h2 {
  font-size: 20px;
}

:local .support-tab h3 {
  font-size: 16px;
}

:local .support-tab h4 {
  font-size: 13px;
  font-weight: 700;
}

:local .support-tab h5 {
  font-size: 11px;
  text-transform: uppercase;
}

:local .support-tab .support-content {
  padding: 0;
}

:local .support-tab .support-content input,
:local .support-tab .support-content textarea {
  font-size: 1.25em;
  font-weight: 200;
  padding: 0.5em 0.75em;
}

:local .support-tab .support-content :global .nmbl-grid-view input {
  font-size: 1em;
  padding: 0.25em 0.5em;
}

