:local .case-resolver .textbox-wrapper {
  position: relative;
  width: 70%;
}

:local .case-resolver .textbox-wrapper textarea {
  width: 100%;
  height: 200px;
}

:local .case-resolver .textbox-wrapper .sending {
  top: 10px;
  left: 200px;
  position: absolute;
}

:local .case-resolver button {
  font-size: 14px;
  margin-right: 1em;
}

:local .case-resolver.busy-sending textarea {
  opacity: 0.25;
}

