@import "vendor/foundation/import-variables";
:global .nmbl-grid-view {
    font-size: 13px;

    br { clear: both; }

    table {
        table-layout: fixed;
        width: 100%;
        white-space: nowrap;
        border-collapse: collapse;
    }

    tr {
        background-color: $white;
        color: $dark-grey-02;

        &.gv-subquery-filters {
            input { width: 100%; }
        }
    }

    thead tr {
        border-bottom: 1px solid $light-grey-01;
    }

    tbody tr {
        border-bottom: 1px solid $light-grey-03;
    }

    th {
        border-right: 1px solid $light-grey-03;
        font-size: 11px;
        text-transform: uppercase;
        font-weight: inherit;
        text-align: left;

        &:not([data-group-header]) {
            padding: 7px;
            background-color: $white;
            border-bottom: 2px solid #999;
        }

        &[data-group-header] {
            padding: 2px 7px;
            background-color: $light-grey-03;
        }

        &[data-sortable] {
            cursor: pointer;
        }

        &:last-child {
            border-right: none;
        }

        &.sorted {
            background-color: $light-grey-05;
        }

        &[data-colname] {
            &:hover { background-color: $light-grey-05; }
        }

        .unsorted:before {
            content: " ";
        }

        .fa-sort-asc, .fa-sort-desc {
            font-size: 12px;
            color: $medium-grey-02;
        }

        [type=color], [type=date], [type=datetime-local], [type=datetime],
        [type=email], [type=month], [type=number], [type=password], [type=search],
        [type=tel], [type=text], [type=time], [type=url], [type=week],
        textarea, select {
            margin: 0;
        }

    }

    th, td {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    td {
        padding: 1em 0.5em;
    }

    .nmbl-grid-view-footer {
        border-top: 1px solid $light-grey-03;
        min-height: 30px;
        background-color: $light-grey-05;
        padding: 10px;
    }

    .sortIcon {
        color: $medium-grey-03;
    }
}
