:local .support-create-case {
  position: relative;
}

:local .support-create-case .create-banner {
  position: relative;
  background-color: #eee;
  padding: 30px 0 30px 10%;
  margin-bottom: 24px;
}

:local .support-create-case .create-banner h2 {
  font-size: 28px;
  margin-bottom: 20px;
}

:local .support-create-case .content-inset {
  padding-top: 3%;
  padding-bottom: 2%;
  margin: 0% 10% 0% 10%;
}

:local .support-create-case .content-inset form.measured {
  width: 40em;
}

:local .support-create-case .content-inset form.measured :global .type-ahead-input input[disabled] {
  background-color: #eee;
  font-color: #444;
}

:local .support-create-case .content-inset form.measured :global .type-ahead-input ul.type-ahead-results {
  font-size: 1.25em;
  font-weight: 200;
}

:local .support-create-case .content-inset form.measured .severity-buttons {
  margin-bottom: 24px;
}

:local .support-create-case .content-inset form.measured .severity-buttons label {
  font-weight: normal;
}

:local .support-create-case .content-inset form.measured .severity-buttons label span {
  font-weight: bold;
}

:local .support-create-case .content-inset form.measured .severity-buttons input[type=radio] {
  margin-right: 10px;
}

:local .support-create-case .content-inset form.measured :global .nmbl-dropdown {
  margin-bottom: 24px;
}

:local .support-create-case .content-inset form.measured h3 {
  margin-bottom: 0.5em;
}

:local .support-create-case .content-inset form.measured p {
  margin: 1em 0 0.25em 0;
}

:local .support-create-case .content-inset form.measured input[type=text],
:local .support-create-case .content-inset form.measured textarea {
  border: 1px solid #E3E6EA;
  margin-bottom: 24px;
  width: 460px;
}

:local .support-create-case .content-inset form.measured input[type=text] {
  padding: 0 0.5em;
}

:local .support-create-case .content-inset form.measured textarea[disabled] {
  background-color: #eee;
  font-color: #444;
}

:local .support-create-case .content-inset form.measured .assets {
  width: 460px;
}

:local .support-create-case .content-inset form.measured button {
  font-size: 14px;
  margin-right: 1em;
}

:local .support-create-case .kcs-suggestions {
  position: absolute;
  top: 40px;
  left: 10%;
  width: 30em;
  margin-left: 43em;
}

:local .support-create-case .kcs-suggestions .suggestion-card,
:local .support-create-case .kcs-suggestions .read-suggestion-card {
  font-size: 15px;
  cursor: pointer;
  color: #fff;
  background-color: #696969;
  padding: 8% 10%;
  margin-bottom: 2em;
  box-shadow: 4px 4px 10px 0px #dcdcdc;
}

:local .support-create-case .kcs-suggestions .suggestion-card:hover,
:local .support-create-case .kcs-suggestions .read-suggestion-card:hover {
  background-color: #78b0e0;
}

:local .support-create-case .kcs-suggestions .read-suggestion-card {
  background-color: #afafaf;
}

:local .support-create-case .kcs-suggestions .read-suggestion-card:hover {
  background-color: #afafaf;
}

