@import "vendor/foundation/import-variables";
// CaseSummaryHeader.scss

:local .case-section {

    margin-top: 1.8em;
    margin-bottom: 2em;

    h2,h3 {
        float: left;
        padding-right: 1em;
    }

    .sub-text {
        float: left;
        right: 10px;
        position: relative;
        padding-right: 10px;
        top: 5px;
        font-size: smaller;
    }

    .extend-line {
        overflow: auto;
        height: 14px;
        border-bottom: 2px solid #ccc;
    }

    .child-content {
        clear: left;
        margin-top: 1.5em;
        overflow: hidden;
        white-space: normal;

        span {
            padding-right: 20px;
        }
    }

}
