@import "vendor/foundation/import-variables";
:local .type-ahead-input {

    position: relative;

    input {

    }

    ul.type-ahead-results {
        top: 2em;
        left:0; bottom:0;

        margin-top: -24px;
        padding:0;

        width: 460px;
        height: 10em;
        background-color: white;
        border: 1px solid #bbb;

        overflow: auto;
        overflow-y: scroll;

        list-style-type: none;

        box-shadow: 0 6px 12px rgba(0,0,0,0.175);

        li {

            a {
                color: #2371ad;
                display: inline-block;
                width: 100%;
                text-decoration: none;
                padding: 0.25em 0.5em;

                &.selected{
                    background-color: #e6e6e6;
                }
            }

        }
    }
}
