@import "vendor/foundation/import-variables";
// CaseSummaryHeader.scss

:local .add-comment {

    .textbox-wrapper {
        position: relative;
        width: 70%;

        textarea { width: 100%; height: 175px}

        .sending {
            top: 20px;
            left: 200px;
            position: absolute;
        }
    }

    button {
        font-size: 14px;
        margin-right: 1em;
    }

    &.busy-sending {
        textarea { opacity: 0.25; }
    }
}
