:local .case-information .case-banner {
  position: relative;
  padding: 28px 0 36px 10%;
  margin-bottom: 1.9em;
  background-color: #eee;
}

:local .case-information .case-banner h4 {
  margin-bottom: 2px;
  font-weight: 400;
  font-size: 1.2em;
}

:local .case-information .case-banner h2 {
  font-size: 28px;
  margin-top: 4px;
}

:local .case-information .case-banner .support-logo {
  position: absolute;
  top: 26px;
  left: 3.5%;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  padding-top: 9px;
  background: #fff url("../../../../../../images/case-open.png") center center no-repeat;
  background-size: 32px;
}

:local .case-information .case-banner .support-logo.closed {
  background-image: url("../../../../../../images/case-closed.png");
}

:local .case-information .page-inset {
  margin: 0 10%;
}

:local .case-information .action-set button {
  font-size: 14px;
  font-weight: 400;
  margin: 0 0.5em 0 0;
  width: 118px;
}

:local .case-information .action-set button > i {
  margin-right: 2px;
}

