:global .nmbl-grid-view {
  font-size: 13px;
}

:global .nmbl-grid-view br {
  clear: both;
}

:global .nmbl-grid-view table {
  table-layout: fixed;
  width: 100%;
  white-space: nowrap;
  border-collapse: collapse;
}

:global .nmbl-grid-view tr {
  background-color: #fff;
  color: #444;
}

:global .nmbl-grid-view tr.gv-subquery-filters input {
  width: 100%;
}

:global .nmbl-grid-view thead tr {
  border-bottom: 1px solid #e2e6ea;
}

:global .nmbl-grid-view tbody tr {
  border-bottom: 1px solid #eee;
}

:global .nmbl-grid-view th {
  border-right: 1px solid #eee;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: inherit;
  text-align: left;
}

:global .nmbl-grid-view th:not([data-group-header]) {
  padding: 7px;
  background-color: #fff;
  border-bottom: 2px solid #999;
}

:global .nmbl-grid-view th[data-group-header] {
  padding: 2px 7px;
  background-color: #eee;
}

:global .nmbl-grid-view th[data-sortable] {
  cursor: pointer;
}

:global .nmbl-grid-view th:last-child {
  border-right: none;
}

:global .nmbl-grid-view th.sorted {
  background-color: #fafafa;
}

:global .nmbl-grid-view th[data-colname]:hover {
  background-color: #fafafa;
}

:global .nmbl-grid-view th .unsorted:before {
  content: " ";
}

:global .nmbl-grid-view th .fa-sort-asc,
:global .nmbl-grid-view th .fa-sort-desc {
  font-size: 12px;
  color: #888;
}

:global .nmbl-grid-view th [type=color],
:global .nmbl-grid-view th [type=date],
:global .nmbl-grid-view th [type=datetime-local],
:global .nmbl-grid-view th [type=datetime],
:global .nmbl-grid-view th [type=email],
:global .nmbl-grid-view th [type=month],
:global .nmbl-grid-view th [type=number],
:global .nmbl-grid-view th [type=password],
:global .nmbl-grid-view th [type=search],
:global .nmbl-grid-view th [type=tel],
:global .nmbl-grid-view th [type=text],
:global .nmbl-grid-view th [type=time],
:global .nmbl-grid-view th [type=url],
:global .nmbl-grid-view th [type=week],
:global .nmbl-grid-view th textarea,
:global .nmbl-grid-view th select {
  margin: 0;
}

:global .nmbl-grid-view th,
:global .nmbl-grid-view td {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

:global .nmbl-grid-view td {
  padding: 1em 0.5em;
}

:global .nmbl-grid-view .nmbl-grid-view-footer {
  border-top: 1px solid #eee;
  min-height: 30px;
  background-color: #fafafa;
  padding: 10px;
}

:global .nmbl-grid-view .sortIcon {
  color: #ccc;
}

