@import "vendor/foundation/import-variables";
// CommentItem.scss

$baloon-color: #f7f7f7;

:local .comment-item {

    position: relative;
    margin: 0.5em 0 1.5em 0;
    padding: 0 200px;
    padding-right: 25%;


    .attribution {
        position: absolute;
        width: 200px;
        background-color: #fff;

        .user-icon {
            position: absolute;
            top: 6px;
            left: initial; right: 5px;

            :global .fa-user {
                color: #aaa;
                font-size: 32px;
            }

        }

        .caret {
            position: absolute;
            top: 2px;
            border: 12px solid transparent;
        }
    }

    .comment-text {
        padding: 1.25em 2em;
        padding-left: 1.5em;
        background-color: $baloon-color;
        margin-left: 10px;
        margin-right: 10em;

        p {
            white-space: pre-line;
        }
    }


    .attribution {
        top: 10px;
        left: 0px; right: initial;
        text-align: right;
        padding-left: 0;
        padding-right: 36px;

        .user-icon {
            left: initial; right: 3px;

        }

        .caret {
            right: -10px; left: initial;
            border-right-color: $baloon-color;
        }

    }


    &.from-nimble {
        padding-left: 25%;
        padding-right: 200px
;
        .attribution {
            text-align: left;
            left: initial; right: -10px;
            padding-right: 0;
            padding-left: 36px;

            .user-icon {
                left: 3px; right: initial;
                :global .fa-user {
                    color: #8fb866;
                }
            }

            .caret {
                left: -10px; right: initial;
                border-left-color: $baloon-color;
            }

            span {
                white-space: pre-line;
            }
        }
        .comment-text {
            margin-right: 0;
            margin-left: 10em;
        }

    }

}
