.banners-container {
  font-size: 16px;
  display: inline-block !important;
  width: 100%;
}

.banners-container .toolbar {
  margin-bottom: 15px;
}

.banners-container BUTTON.ui-widget .ui-button-text {
  padding: .2em .8em;
}

.banners-container INPUT.error {
  border: 1px dotted red;
}

.banners-container LABEL.error:before {
  content: "\f071";
  font-family: "FontAwesome";
  color: #e85757;
  margin: 0 5px 0 5px;
}

.banners-container LABEL.error {
  color: #e85757;
}

.banners-container .label-group {
  margin-bottom: 10px;
}

#edit-banner-form {
  font-size: 16px;
  background-color: white;
}

#edit-banner-form input.text {
  margin-bottom: 12px;
  width: 95%;
  padding: .4em;
}

#edit-banner-form #accountName {
  width: 60%;
  display: inline;
  float: left;
  margin-right: 10px;
}

#edit-banner-form #accountId {
  width: 25%;
}

#edit-banner-form label,
#edit-banner-form input {
  display: block;
}

#edit-banner-form .error {
  margin-bottom: 15px;
  color: red;
}

#edit-banner-form input.error {
  background-color: red;
}

#edit-banner-form .form-group {
  margin-bottom: 1rem;
}

.banner-notify-dialog.ui-dialog {
  font-size: 16px;
  box-shadow: 0 0 0.77rem #444;
  z-index: 500;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar {
  background-color: #666;
  font-weight: bold;
  color: #fff;
  display: flex;
  padding: 10px 20px;
  justify-content: space-between;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar .ui-dialog-titlebar-close.ui-button {
  position: initial;
  background: none;
  height: initial;
  width: initial;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar .ui-dialog-titlebar-close.ui-button .ui-icon {
  display: none;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar .ui-state-hover {
  background-color: #666;
  cursor: pointer;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar .ui-dialog-title {
  font-size: 18px;
}

.banner-notify-dialog.ui-dialog .ui-dialog-titlebar .ui-dialog-titlebar-close {
  color: #fff;
}

#banner-notify {
  font-size: 16px;
  background-color: #fff;
}

#banner-notify #banner-title {
  font-weight: bold;
}

#banner-notify #status {
  margin-right: 5px;
}

#banner-notify #button-list button:not(:last-of-type) {
  margin-right: 10px;
}

#banner-notify #notify p {
  padding-top: 1em;
}

