.container {
  font-size: 16px;
  overflow-x: hidden;
}

.container :global .label {
  display: inline;
  padding: .2em .6em .3em;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;
}

.container :global .label-warning {
  background-color: #fc9935;
  color: #fff;
}

.container :global .breadcrumb .fa-info-circle {
  margin-left: 6px;
}

.container :global .lab-app {
  position: relative;
}

.container :global .lab-app button.feedbacking {
  transition: color 0.1s linear, background-color 0.1s linear;
  margin-right: 10px;
}

.container :global .lab-app button.feedbacking i {
  margin-left: 4px;
  vertical-align: bottom;
}

.container :global .lab-app .shiny-container {
  position: relative;
  border: 1px solid #cdcdcd;
}

.container :global .lab-app .shiny-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  border: 0;
}

.container :global .lab-app .shiny-container .lab-app-loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: url(../../../../../images/labs-loading.gif) no-repeat center center;
  background-size: 100px;
}

.container :global .labs-view {
  position: relative;
  margin: 0px -16px 0 -16px;
  padding-bottom: 180px;
  /**
             * Labs Landing Page Menu
             */
}

.container :global .labs-view .label {
  text-transform: uppercase;
}

.container :global .labs-view .labs-landing {
  -webkit-font-smoothing: antialiased;
  width: 1110px;
  margin: 0 auto;
  /*
                Menu bar
                height: 30px
                top and bottom 1px line: #ccc
                Selected menu item: 11px Bold #444
                Default menu items: 11px #888, on hover, #444
                */
}

.container :global .labs-view .labs-landing .banner {
  position: relative;
  height: 106px;
  background-size: 130px;
  background-repeat: no-repeat;
  background-position: 45% 72%;
  background-image: url(../../../../../images/labs-logo.svg);
}

.container :global .labs-view .labs-landing .banner .label {
  position: absolute;
  top: 54px;
  right: 472px;
}

.container :global .labs-view .labs-landing h1 {
  margin: 0 0 26px 0;
  color: #444;
  text-align: center;
}

.container :global .labs-view .labs-landing ul {
  margin: 0;
  list-style: none;
  padding: 0;
}

.container :global .labs-view .labs-landing ul.labs-nav {
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 4px 0;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  text-align: center;
  text-transform: uppercase;
}

.container :global .labs-view .labs-landing ul.labs-nav li {
  display: inline;
  margin: 0 1em;
}

.container :global .labs-view .labs-landing ul.labs-nav li a {
  color: #444;
  cursor: pointer;
}

.container :global .labs-view .labs-landing ul.labs-nav li a:hover {
  text-decoration: underline;
}

.container :global .labs-view .labs-landing ul.labs-nav li a.selected {
  font-weight: bold;
}

.container :global .labs-view .labs-landing ul.labs-apps li {
  font-size: 1em;
  display: inline-block;
  vertical-align: top;
}

.container :global .labs-view .labs-landing ul.labs-apps li a {
  position: relative;
  display: block;
  margin: 10px 10px;
  padding: 30px;
  cursor: pointer;
  background-color: #fff;
  width: 350px;
  height: 200px;
  overflow: auto;
  border: 1px solid transparent;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.15);
  transform: translate(0, 0);
  transition: transform 0.1s ease-in-out;
}

.container :global .labs-view .labs-landing ul.labs-apps li a:hover {
  text-decoration: none;
  border: 1px solid #1973cb;
  box-shadow: 0 2px 5px 0 rgba(25, 115, 203, 0.5);
  transform: translate(0, -2px);
}

.container :global .labs-view .labs-landing ul.labs-apps li a:focus {
  outline: none;
  text-decoration: none;
}

.container :global .labs-view .labs-landing ul.labs-apps li a h3 {
  color: #1973cb;
  font-weight: 700;
  margin: 0 0 6px 0;
  padding: 0;
}

.container :global .labs-view .labs-landing ul.labs-apps li a .starset {
  height: 14px;
  margin: 4px 0;
}

.container :global .labs-view .labs-landing ul.labs-apps li a p {
  color: #444;
}

.container :global .labs-view .labs-landing ul.labs-apps li a .lab-app-views {
  color: #888;
}

.container :global .labs-view .labs-landing ul.labs-apps li a .label {
  position: absolute;
  top: 5px;
  right: 5px;
}

.container :global .lab-app iframe {
  border: none;
}

.container :global #app[data-user-class="external"] ul.labs-apps li[data-app-id="performance_recommendations_storage"] {
  display: none !important;
}

:global .lab-app-feedback {
  font-size: 16px;
}

:global .lab-app-feedback #contact-form .form-group {
  margin-bottom: 24px;
}

:global .lab-app-feedback #contact-form label {
  margin-top: 0;
  padding-top: 0;
  padding-bottom: 7px;
}

:global .lab-app-feedback #contact-form .frequency label {
  margin-right: 14px;
  font-weight: normal;
  cursor: pointer;
}

:global .lab-app-feedback .labs-ratings-template {
  font-size: 1.25em;
  position: absolute;
  right: 10px;
  top: 2px;
}

:global .lab-app-feedback .labs-ratings-template a {
  color: #ccc;
  display: inline-block;
  text-decoration: none;
  margin: 0 4px;
}

:global .lab-app-feedback .labs-ratings-template a.fa-thumbs-up:hover,
:global .lab-app-feedback .labs-ratings-template a.fa-thumbs-up.selected {
  color: #01a982;
}

:global .lab-app-feedback .labs-ratings-template a.fa-thumbs-down:hover,
:global .lab-app-feedback .labs-ratings-template a.fa-thumbs-down.selected {
  color: #f04953;
}

:global .lab-app-feedback .rating {
  font-size: 1em;
  cursor: pointer;
}

:global .lab-app-feedback .rating .starduo {
  font-size: 1.5em;
  position: relative;
  display: inline-block;
  width: 1em;
  height: 1em;
}

:global .lab-app-feedback .rating .starduo .fa.fa-star,
:global .lab-app-feedback .rating .starduo .fa.fa-star-o {
  position: absolute;
  top: 0;
  left: 0;
  width: 1em;
  height: 1em;
}

:global .lab-app-feedback .rating .starduo .fa-star {
  color: transparent;
}

:global .lab-app-feedback .rating .starduo .fa-star-o {
  color: #777;
}

:global .lab-app-feedback .rating .star-is-on .fa-star,
:global .lab-app-feedback .rating[data-score-current="1"] .starduo[data-score="1"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="2"] .starduo[data-score="1"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="2"] .starduo[data-score="2"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="1"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="2"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="3"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="1"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="2"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="3"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="4"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="1"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="2"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="3"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="4"] .fa-star,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="5"] .fa-star {
  color: #fc9935;
}

:global .lab-app-feedback .rating .star-is-on .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="1"] .starduo[data-score="1"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="2"] .starduo[data-score="1"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="2"] .starduo[data-score="2"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="1"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="2"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="3"] .starduo[data-score="3"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="1"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="2"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="3"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="4"] .starduo[data-score="4"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="1"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="2"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="3"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="4"] .fa-star-o,
:global .lab-app-feedback .rating[data-score-current="5"] .starduo[data-score="5"] .fa-star-o {
  color: transparent;
  visibility: hidden;
}

:global .lab-app-feedback .rating[data-score-current]:hover .starduo[data-score]:hover .fa-star {
  color: #fc9935;
}

:global .lab-app-feedback .rating[data-score-current]:hover .starduo[data-score]:hover .fa-star-o {
  color: #f04953;
  visibility: visible;
}

:global .lab-app-feedback .rating[data-score-current]:hover .starduo[data-score]:active .fa-star {
  color: #f04953;
}

:global .lab-app-feedback .rating[data-score-current]:hover .starduo[data-score]:active .fa-star-o {
  color: #fc9935;
  visibility: visible;
}

:global .lab-app-user-guide {
  font-size: 16px;
}

:global .lab-app-user-guide .modal-body > ul {
  padding-left: 40px;
}

