:global .nmbl-Pagination {
  color: #444;
  margin: 0;
}

:global .nmbl-Pagination .pagination {
  margin-left: 0;
  margin-bottom: 1rem;
}

:global .nmbl-Pagination .pagination::before,
:global .nmbl-Pagination .pagination::after {
  display: table;
  content: ' ';
  flex-basis: 0;
  order: 1;
}

:global .nmbl-Pagination .pagination::after {
  clear: both;
}

:global .nmbl-Pagination .pagination li {
  margin-right: 0.07143rem;
  border-radius: 0;
  font-size: 1rem;
  display: none;
}

:global .nmbl-Pagination .pagination li:last-child,
:global .nmbl-Pagination .pagination li:first-child {
  display: inline-block;
}

@media print, screen and (min-width: 40em) {
  :global .nmbl-Pagination .pagination li {
    display: inline-block;
  }
}

:global .nmbl-Pagination .pagination a,
:global .nmbl-Pagination .pagination button {
  display: block;
  padding: 0.21429rem 0.71429rem;
  border-radius: 0;
  color: #000;
}

:global .nmbl-Pagination .pagination a:hover,
:global .nmbl-Pagination .pagination button:hover {
  background: #e2e6ea;
}

:global .nmbl-Pagination .pagination .current {
  padding: 0.21429rem 0.71429rem;
  background: #01a982;
  color: #fff;
  cursor: default;
}

:global .nmbl-Pagination .pagination .disabled {
  padding: 0.21429rem 0.71429rem;
  color: #666;
  cursor: not-allowed;
}

:global .nmbl-Pagination .pagination .disabled:hover {
  background: transparent;
}

:global .nmbl-Pagination .pagination .ellipsis::after {
  padding: 0.21429rem 0.71429rem;
  content: '\2026';
  color: #000;
}

:global .nmbl-Pagination .pagination-previous a::before,
:global .nmbl-Pagination .pagination-previous.disabled::before {
  display: inline-block;
  margin-right: 0.5rem;
  content: '\00ab';
}

:global .nmbl-Pagination .pagination-next a::after,
:global .nmbl-Pagination .pagination-next.disabled::after {
  display: inline-block;
  margin-left: 0.5rem;
  content: '\00bb';
}

:global .nmbl-Pagination .pagination {
  display: inline-block;
  padding: 0;
}

:global .nmbl-Pagination .pagination-previous,
:global .nmbl-Pagination .pagination-next {
  width: 1.25rem;
  height: 1rem;
  text-align: center;
  line-height: 1;
}

:global .nmbl-Pagination .pagination-previous > a,
:global .nmbl-Pagination .pagination-next > a {
  color: inherit;
  text-decoration: none;
}

:global .nmbl-Pagination .pagination-previous > a:not(.disabled):hover,
:global .nmbl-Pagination .pagination-next > a:not(.disabled):hover {
  color: #fff;
  border-color: #e2e6ea;
}

:global .nmbl-Pagination .pagination-previous > a.disabled,
:global .nmbl-Pagination .pagination-next > a.disabled {
  cursor: default;
}

:global .nmbl-Pagination .pagination-previous > a {
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px;
  border: 1px solid #ccc;
}

:global .nmbl-Pagination .pagination-next > a {
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  border: 1px solid #ccc;
  border-left: 0;
}

:global .nmbl-Pagination .pagination-previous > a::before,
:global .nmbl-Pagination .pagination-next > a::after {
  content: none;
}

:global .nmbl-Pagination .nmbl-Pagination__count {
  color: #666;
}

:global .nmbl-Pagination .nmbl-Pagination__range {
  margin-right: 10px;
}

