:local .case-section {
  margin-top: 1.8em;
  margin-bottom: 2em;
}

:local .case-section h2,
:local .case-section h3 {
  float: left;
  padding-right: 1em;
}

:local .case-section .sub-text {
  float: left;
  right: 10px;
  position: relative;
  padding-right: 10px;
  top: 5px;
  font-size: smaller;
}

:local .case-section .extend-line {
  overflow: auto;
  height: 14px;
  border-bottom: 2px solid #ccc;
}

:local .case-section .child-content {
  clear: left;
  margin-top: 1.5em;
  overflow: hidden;
  white-space: normal;
}

:local .case-section .child-content span {
  padding-right: 20px;
}

