:local .support-loading {
  position: relative;
  height: 32em;
}

:local .support-loading .loading-box {
  margin: 3em auto 0 auto;
  width: 25em;
  height: 12em;
  color: #444;
}

:local .support-loading .loading-box hr {
  margin: 10px -3em;
  border-color: #6dc16d;
}

:local .support-loading .loading-box hr.error {
  border-color: #b72020;
}

:local .support-loading .loading-box p.wait-text {
  text-align: center;
}

