.container {
  font-size: 16px;
}

.container :global .permissions-view .requested-grants dl dt {
  width: 100px;
  float: left;
  clear: left;
  text-align: right;
  margin-bottom: 0;
}

.container :global .permissions-view .requested-grants dl dd {
  margin-left: 110px;
}

.container :global .permissions-view .grid-view h4 {
  margin: 0px;
}

.container :global .permissions-view [data-region="requested"] > div,
.container :global .permissions-view [data-region="inbound"] > div,
.container :global .permissions-view [data-region="outbound"] > div {
  margin-bottom: 3em;
}

