:local .comment-item {
  position: relative;
  margin: 0.5em 0 1.5em 0;
  padding: 0 200px;
  padding-right: 25%;
}

:local .comment-item .attribution {
  position: absolute;
  width: 200px;
  background-color: #fff;
}

:local .comment-item .attribution .user-icon {
  position: absolute;
  top: 6px;
  left: initial;
  right: 5px;
}

:local .comment-item .attribution .user-icon :global .fa-user {
  color: #aaa;
  font-size: 32px;
}

:local .comment-item .attribution .caret {
  position: absolute;
  top: 2px;
  border: 12px solid transparent;
}

:local .comment-item .comment-text {
  padding: 1.25em 2em;
  padding-left: 1.5em;
  background-color: #f7f7f7;
  margin-left: 10px;
  margin-right: 10em;
}

:local .comment-item .comment-text p {
  white-space: pre-line;
}

:local .comment-item .attribution {
  top: 10px;
  left: 0px;
  right: initial;
  text-align: right;
  padding-left: 0;
  padding-right: 36px;
}

:local .comment-item .attribution .user-icon {
  left: initial;
  right: 3px;
}

:local .comment-item .attribution .caret {
  right: -10px;
  left: initial;
  border-right-color: #f7f7f7;
}

:local .comment-item.from-nimble {
  padding-left: 25%;
  padding-right: 200px;
}

:local .comment-item.from-nimble .attribution {
  text-align: left;
  left: initial;
  right: -10px;
  padding-right: 0;
  padding-left: 36px;
}

:local .comment-item.from-nimble .attribution .user-icon {
  left: 3px;
  right: initial;
}

:local .comment-item.from-nimble .attribution .user-icon :global .fa-user {
  color: #8fb866;
}

:local .comment-item.from-nimble .attribution .caret {
  left: -10px;
  right: initial;
  border-left-color: #f7f7f7;
}

:local .comment-item.from-nimble .attribution span {
  white-space: pre-line;
}

:local .comment-item.from-nimble .comment-text {
  margin-right: 0;
  margin-left: 10em;
}

