@import "vendor/foundation/import-variables";
// SupportCreateCase.scss

$right-panel-width: 30%;

:local .support-create-case {

    position: relative;

    .create-banner {

        position: relative;
        background-color: #eee;
        padding: 30px 0 30px 10%;
        margin-bottom: 24px;

        h2 {
            font-size: 28px;
            margin-bottom: 20px;
        }

    }

    .content-inset {

        $form-element-below-spacing: 24px;

        padding-top: 3%;
        padding-bottom: 2%;
        margin: 0% 10% 0% 10%;

        form.measured {

            width: 40em;

            :global .type-ahead-input {

                input[disabled] {
                    background-color: #eee;
                    font-color: #444;
                }

                // input { width: 90%; }

                ul.type-ahead-results {
                    font-size: 1.25em;
                    font-weight: 200;
                }

            }

            .severity-buttons {
                margin-bottom: $form-element-below-spacing;
                label {
                    font-weight: normal;
                    span {
                        font-weight: bold;
                    }
                }
                input[type=radio] {
                    margin-right: 10px;
                }
            }

            :global .nmbl-dropdown {
                margin-bottom: $form-element-below-spacing;
            }

            h3 {
                margin-bottom: 0.5em;
            }

            p {
                margin: 1em 0 0.25em 0;
            }

            // normalize
            input[type=text],
            textarea {
                border: 1px solid #E3E6EA;
                margin-bottom: $form-element-below-spacing;
                width: 460px;
            }

            input[type=text] {
                padding: 0 0.5em;
            }

            textarea[disabled] {
                background-color: #eee;
                font-color: #444;
            }

            .assets {
                width: 460px;
            }

            button {
                font-size: 14px;
                margin-right: 1em;
            }
        }

    }

    .kcs-suggestions {
        position: absolute;
        top: 40px;
        left: 10%;
        width: 30em;
        margin-left: 43em;

        .suggestion-card {
            font-size: 15px;
            cursor: pointer;
            color: #fff;
            background-color: #696969;
            padding: 8% 10%;
            margin-bottom: 2em;
            box-shadow: 4px 4px 10px 0px #dcdcdc;

            &:hover {
                background-color: #78b0e0
            }

        }
        .read-suggestion-card {
            @extend .suggestion-card;
            background-color: #afafaf;

            &:hover {
                background-color: #afafaf
            }
        }

        // .suggestion-card {
        //     transition: transform,opacity 2s ease-in;

        //     &::empty{
        //         transform: translate(-350px,0);
        //         opacity: 0;
        //     }

        //     &:not(:empty){
        //         transform: translate(0,0);
        //         opacity: 1;
        //     }
        // }

    }

}
