.legacy-breadcrumbs :global .breadcrumbs-region {
  font-size: 18px;
  font-weight: 700;
  margin: 0px;
  position: relative;
}

.legacy-breadcrumbs :global .breadcrumb {
  color: #666666;
  font-weight: bold;
  margin-bottom: 5px;
  padding: 8px 15px 5px 15px;
}

.legacy-breadcrumbs :global .breadcrumb > li {
  display: inline-block;
}

.legacy-breadcrumbs :global .breadcrumb > li:not(:last-of-type):after {
  content: "\00a0/\00a0";
}

.legacy-breadcrumbs :global .breadcrumb > .active {
  color: #666666;
}

