@import "vendor/foundation/import-variables";
.banners-container {
    font-size: $next-global-font-size;
    display: inline-block !important;
    width: 100%;

    .toolbar {
        margin-bottom: 15px;
    }

    BUTTON.ui-widget {
        .ui-button-text {
            padding: .2em .8em;
        }
    }

    INPUT.error {
        border: 1px dotted red;
    }

    LABEL.error:before {
        content: "\f071";
        font-family: "FontAwesome";
        color: rgb(232, 87, 87);
        margin: 0 5px 0 5px;
    }

    LABEL.error {
        color: rgb(232, 87, 87);
    }


    .label-group {
        margin-bottom: 10px;
    }

    #banners-grid-container {

    }
}

#edit-banner-form {
    font-size: $next-global-font-size;

    background-color: white;


    input.text {
        margin-bottom:12px;
        width:95%;
        padding: .4em;
    }

    #accountName {
        width: 60%;
        display: inline;
        float: left;
        margin-right: 10px;
    }

    #accountId {
        width:25%;
    }

    label, input {
        display: block;
    }

    .error {
        margin-bottom: 15px;
        color: red;
    }

    input.error {
        background-color: red;
    }

    .form-group {
        margin-bottom: 1rem;
    }
}

.banner-notify-dialog.ui-dialog {
    font-size: $next-global-font-size;
    box-shadow: 0 0 0.77rem $dark-grey-02;
    z-index: 500;

    // Defeat Piano's jquery-ui styles with specificity
    .ui-dialog-titlebar {
        background-color: $medium-grey-01;
        font-weight: bold;
        color: $white;
        display: flex;
        padding: 10px 20px;
        justify-content: space-between;

        .ui-dialog-titlebar-close.ui-button {
            position: initial;
            background: none;
            height: initial;
            width: initial;

            .ui-icon {
                display: none;
            }
        }

        .ui-state-hover {
            background-color: $medium-grey-01;
            cursor: pointer;
        }

        .ui-dialog-title {
            font-size: 18px;
        }

        .ui-dialog-titlebar-close {
            color: $white;
        }
    }
}

#banner-notify {
    font-size: $next-global-font-size;
    background-color: $white;

    #banner-title {
        font-weight: bold;
    }

    #status {
        margin-right: 5px;
    }

    #button-list {
        button:not(:last-of-type) {
            margin-right: 10px;
        }
    }

    #notify p {
        padding-top: 1em;
    }
}
