:local .search-nav {
  position: relative;
  padding: 30px 0 85px 5%;
  height: 6em;
}

:local .search-nav .actions-options {
  position: absolute;
  top: 30px;
  right: 5%;
}

:local .search-nav .actions-options button {
  font-size: 15px;
  font-weight: 400;
  height: 33px;
  margin: 0 0 0 0.5em;
}

:local .search-nav .actions-options button > i {
  margin-right: 6px;
}

:local .search-nav .case-owner {
  position: absolute;
  top: 30px;
}

:local .search-nav .case-owner :global .cases-by button {
  color: white;
  font-size: 15px;
  background-color: #01a982;
  border-color: #999;
  height: 33px;
}

:local .search-nav .case-owner :global .cases-by button:hover,
:local .search-nav .case-owner :global .cases-by button:focus {
  background-color: #016c53;
}

:local .search-nav .case-owner :global .cases-by button.active {
  background-color: #016c53;
  border-color: #01624b;
}

:local .search-nav .case-owner .filter-label {
  font-weight: bold;
  padding-right: 5px;
}

:local .search-nav .search-stuff {
  width: 35%;
  margin: 0 auto;
}

:local .search-nav .search-stuff input.search-bar {
  display: block;
  width: 90%;
  font-size: 12px;
  border: 0.5px solid #a6a6a6;
  border-radius: 0.5em;
  height: 33px;
}

:local .search-nav .search-stuff .cancel-search {
  position: absolute;
  top: 30px;
  right: 35%;
  color: #6d6d6d;
  width: 20px;
  height: 20px;
  text-align: center;
  text-decoration: none;
  padding: 6px;
}

:local .search-nav .hide-auto-cases {
  position: absolute;
  top: 70px;
  opacity: 1;
}

:local .search-nav .hide-auto-cases input {
  margin-right: 5px;
}

:local .search-nav.is-internal .hide-auto-cases {
  top: 35px;
}

:local .search-nav .local-stuff {
  background-color: #eee;
  height: 4em;
}

